import uniGrid from '@/components/uni-grid/uni-grid.vue';
export default {
	components: { uniGrid },
	data() {
		return {};
	},
	onLoad() {
		this.loadExecution();
	},
	methods: {
		goService(e) {
			if (e.index == 0) {
				uni.navigateTo({
					url: '../module/rentHouse',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
			if (e.index == 1) {
				uni.navigateTo({
					url: '/pages/module/landlord',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
			if (e.index == 2) {
				uni.navigateTo({
					url: '../module/contract',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
			if (e.index == 3) {
				uni.navigateTo({
					url: '/pages/module/rates',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
			if (e.index == 4) {
				uni.navigateTo({
					url: '/pages/module/pretendfriends',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		rent() {
			uni.navigateTo({
				url: '/pages/detail/detail',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		loadExecution: function() {
			/**
			 * 获取本地存储中launchFlag的值
			 * 若存在，说明不是首次启动，直接进入首页；
			 * 若不存在，说明是首次启动，进入引导页；
			 */
			try {
				const value = uni.getStorageSync('launchFlag');
				if (value) {
					if (value == true) {
						uni.switchTab({
							url: '/pages/index/index'
						});
					} else {
						uni.redirectTo({
							url: '/pages/index/guide'
						});
					}
				} else {
					uni.setStorage({
						key: 'launchFlag',
						data: true,
						success: function() {
							console.log('存储launchFlag');
						}
					});
					uni.redirectTo({
						url: '/pages/index/guide'
					});
				}
			} catch (e) {
				// error
				uni.setStorage({
					key: 'launchFlag',
					data: true,
					success: function() {
						console.log('error时存储launchFlag');
					}
				});
				uni.redirectTo({ url: '/pages/index/guide' });
			}
			return;
			uni.switchTab({
				url: '/pages/index/index'
			});
		}
	}
};
